import React, { useEffect, useState, useRef } from 'react';

import { Label, Textarea, Checkbox } from '@rebass/forms';

import CodeEditorWithOutput from '@components/CodeEditor';
import Button from '@components/Button';

import useScript from './useScript';

import mod from '!!raw-loader!./setupModule.js';

export const CPythonSwitchCase = () => {
  const [load, setLoad] = useState(false);
  const [output, setOutput] = useState('');
  const [editorState, setEditorState] = useState('');
  const [S, setS] = useState([]);

  useEffect(() => {
    console.log('printing load: ', load);
    const scriptCPySc = document.createElement('script');
    const scriptModule = document.createElement('script');
    if (load) {
      console.log('Adding script tag to dom');
      scriptModule.id = 'mod';
      scriptModule.innerHTML = mod;
      document.body.appendChild(scriptModule);

      if (typeof Module != 'undefined') {
        console.log('module exists', Module);
        if (!Module.hasOwnProperty('print') || !Module.hasOwnProperty('printErr')) {
          console.log('adding print method');
          Module['print'] = async function (data) {
            await setOutput(o => o + data + '\n');
            console.log('print', data);
          };
          Module['printErr'] = async function (data) {
            await setOutput(o => o + data + '\n');
            console.log('printErr', data);
          };
        }
      }

      scriptCPySc.id = 'asdf';
      scriptCPySc.src = 'python3.8.js';
      scriptCPySc.async = true;

      document.body.appendChild(scriptCPySc);
    }

    return () => {
      if (load) {
        document.body.removeChild(scriptCPySc);
        document.body.removeChild(scriptModule);
      }
    };
  }, [load]);

  return (
    <div style={{ margin: '25px auto 18px' }}>
      <p>This is just a heading</p>
      <Checkbox
        id="remember"
        name="remember"
        onClick={e => {
          console.log(e);
          console.log(e.target.checked);
        }}
      />
      <br />
      <br />
      <br />
      <br />
      <Button
        onPress={(): never => {
          console.log('button pressed for loading, load = ', load);
          if (!load) setLoad(!load);
        }}
        text={'Load da ting!'}
      />
      <Button
        onPress={(): never => {
          setOutput(o => '');
          if (typeof Module != 'undefined') {
            Module.ccall('PyRun_SimpleString', 'number', ['string'], [editorState], {
              async: true
            });
            console.log('ran code');
            try {
              Module.ccall('PyRun_SimpleString', 'number', ['string'], ['exit()'], {});
            } catch (err) {}
            console.log('exited');
            Module.callMain();
            console.log('called main');
          } else {
            console.log('it not loaded, the module');
          }
        }}
        text={'RUN DA CODE!'}
      />
      <CodeEditorWithOutput setEditorState={setEditorState} output={output} />
    </div>
  );
};
