import CodeEditor from './CodeEditor';
import CodeEditor2 from './CodeEditor2';

import { ControlledEditor } from '@monaco-editor/react';
import React, { useRef, useState, useEffect } from 'react';
import AceEditor from 'react-ace';
import { XTerm } from 'xterm-for-react';
import { FitAddon } from 'xterm-addon-fit';

import { ITAOAThemeUIContext } from '@types';
import styled from '@emotion/styled';

import { mediaquery } from '@styles/media';

import { getWindowDimensions, getBreakpointFromTheme } from '@utils';

require(`ace-builds/src-noconflict/theme-twilight`);
require(`ace-builds/src-noconflict/theme-kuroir`);

const CodeEditorWithOutput: React.FC<{}> = (props: any) => {
  const handleWindowResize = (): void => {
    const { width } = getWindowDimensions();
    const breakpoint = getBreakpointFromTheme('tablet');
    const foldEditorOuput = width <= breakpoint;
    console.log(foldEditorOuput);
  };

  /* React hook to call function that will
   * handle mobile vs desktop view
   */
  useEffect((): (() => void) => {
    window.addEventListener('resize', handleWindowResize);
    return (): void => {
      window.removeEventListener('resize', handleWindowResize);
    };
  }, []);

  const { setEditorState, output } = props;

  const onEditorLoad = editor => {
    // eslint-disable-next-line no-param-reassign
    editor.renderer.$cursorLayer.element.style.display = 'none';
  };

  return (
    <Container>
      <div style={{ float: 'left' }}>
        <AceEditor
          mode="python"
          height="400px"
          width="400px"
          theme="twilight"
          name="editor"
          fontSize={14}
          markers={[]}
          value={``}
          showGutter={true}
          highlightActiveLine={false}
          showPrintMargin={true}
          wrapEnabled={true}
          scrollMargin={[2, 2, 0, 0]}
          onChange={setEditorState}
          setOptions={{
            showLineNumbers: true
          }}
          editorProps={{
            $blockScrolling: Infinity
          }}
        />
      </div>
      <div style={{ float: 'left' }}>
        <AceEditor
          height="400px"
          width="400px"
          readOnly
          theme="kuroir"
          name="output"
          fontSize={14}
          markers={[]}
          value={output}
          showGutter={true}
          showPrintMargin={true}
          highlightActiveLine={false}
          showPrintMargin={true}
          wrapEnabled={true}
          scrollMargin={[2, 2, 0, 0]}
          onLoad={onEditorLoad}
          setOptions={{
            showLineNumbers: false,
            indentedSoftWrap: false,
            displayIndentGuides: false
          }}
          editorProps={{
            $blockScrolling: Infinity
          }}
        />
      </div>
    </Container>
  );
};

const Container = styled.div((p: ITAOAThemeUIContext) => ({
  height: '100vh',
  overflow: 'hidden',
  fontFamily: 'Roboto',
  display: 'flex',
  gridTemplateRows: '1fr 1fr',
  gridTemplateAreas: "'editor' 'output'",
  [mediaquery.tablet()]: {
    display: 'grid'
  }
}));

export default CodeEditorWithOutput;
